.adm {
    color: red;
    position: absolute;
    right: 20%;
    top: 50%;
    transform: translate(0, -50%);
}

nav [aria-describedby] {
    cursor: pointer !important;
}
nav [aria-describedby]:hover {
    transform: scale(1) !important;
}
body {
    overflow-x: hidden !important;
}
#mNavWrap > ul > li:nth-child(2) > ul > li > ul > li:nth-child(4) > a > img {
    margin: 0 20px 0 0;
}
img {
    font-size: 0.5em !important;
    color: gray !important;
}
::-webkit-scrollbar { /* main scrollbar */
    width: 3px !important;
}
::-webkit-scrollbar-thumb {
   -webkit-border-radius: 10px !important;
   border-radius: 0 !important;
   background: linear-gradient(to bottom, rgb(196, 122, 26), rgb(219, 194, 162)) !important; 
}
#mNavWrap > ul > li:nth-child(1) > ul, #mNavWrap > ul > li:nth-child(2) > ul, #mNavWrap > ul > li:nth-child(3) > ul, #mNavWrap > ul > li:nth-child(4) > ul {
    background-color: rgba(0, 140, 255, 0.6);
    box-shadow: 0px 10px 20px 10px rgb(0, 135, 245, 0.9);
    padding: 0 20px 30px 20px;
}
#app > header > div > a {
    margin: auto;
}
#progressBar {
    position: fixed;
    width: 0%;
    height: 3px;
    background-color: rgb(204, 135, 45);
    z-index: 2023;
    top: 0;
    left: 0;
}
header {
    width: 100%;
    height: 40px;
    background: linear-gradient(to left, #18338a, #2098f7);
    display: flex;
    align-items: center;
    padding: 0 15px;
    position: fixed;
    z-index: 2022;
}
header .container {
    margin: auto;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
}
header img {
    width: 30px;
    height: 30px;
    margin: 3px 50px 0 0;
}
#dNavWrap {
    display: none;
}
header .socnetWrap {
    width: 90px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    order: -1;
}
header .socnetWrap i {
    font-size: 1.25em;
}
.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 4px;
  background-color: #fff;
  margin: 7px 0;
  transition: 0.4s;
}
header .active:nth-child(1) {
    margin: auto;
}
#mNavWrap {
    height: 100vh;
    width: 100%;
    position: absolute;
    left: -100%;
    top: 40px;
    background: rgba(32, 152, 247, 0.94);
    transition: 0.5s;
}
#mNavWrap .menuWrap {
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 0 120px 0;
}
#mNavWrap .menuWrap li {
    margin: 20px 0 0 0;
    min-width: 150px;
    min-height: 50px;
    font-size: 1.1em;
    display: flex;
    flex-direction: column;
}
#mNavWrap .menuWrap li ul {
    display: none;
}
#mNavWrap .menuWrap li div:first-child {
    display: flex;
    justify-content: center;
    align-items: center;
}
#mNavWrap .menuWrap li i {
    font-size: 1.4em;
    margin: 0 0 0 20px;
}
#mNavWrap .menuWrap li ul li {
    align-items: center;
}
#mNavWrap .menuWrap li:nth-child(2) ul li div {
    min-width: 100px;
    display: flex;
    justify-content: center;
}
#mNavWrap .menuWrap li:nth-child(2) ul li i {
    margin: 0 0 0 10px;
    font-size: 1.1em;
}
#mNavWrap .menuWrap li:nth-child(2) a .fa-caret-square-down {
    font-size: 1.4em;
}
#mNavWrap .menuWrap li:nth-child(2) ul li a {
    display: flex;
    align-items: center;
    font-size: 0.8em;
}
#mNavWrap .menuWrap li:nth-child(2) ul li ul li a {
    display: flex;
    min-width: 140px;
    justify-content: space-between;
    align-items: center;
}
#mNavWrap .menuWrap li ul li img {
    width: 25px;
    height: 25px;
}
#mNavWrap .menuWrap li ul li a {
    font-size: 0.9em;
}
#mNavWrap .menuWrap li:nth-child(2) ul li div i {
    padding: 0 0 0 15px;
}
#mNavWrap .transfersSubMenu li {
    font-size: 1.1em;
    flex-direction: row;
}
#mNavWrap .transfersSubMenu li img {
    margin: 0 20px -5px 0;
    width: 20px !important;
    height: 20px !important;
}
#mNavWrap .othersSubMenu li {
    font-size: 1.1em;
    flex-direction: row;
}
#mNavWrap .othersSubMenu li img {
    margin: 0 20px -4px 0;
    width: 21px !important;
    height: 19px !important;
}
.subMenuArrowColorToggle {
    color: rgb(204, 135, 45);
}
main {
    padding: 120px 0 0 0;
    display: flex;
    justify-content: space-around;
}
main .container {
    width: 96%;
}
.sidebar div div {
    display: none;
}
nav .actual, nav .actual div a {
    background-color: #f02d54;
    cursor: pointer;
}
nav .actual:hover, nav .actual div a:hover {
    background-color: #f04e6f !important;
}
#mNavWrap .menuWrap li:last-child {
    min-height: 30px;
    display: flex;
    padding: 5px 0 0 0;
}
#dNavWrap > ul > li:nth-child(3) {
    cursor: default;
}
#dNavWrap > ul > li:nth-child(4) {
    cursor: default;
}
.favoriteTeamPopUp {
    position: fixed;
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(20px);
    top: 0;
    left: 0;
    z-index: 2021;
    display: none;
}
.favoriteTeamPopUp .close {
    position: fixed;
    background: rgb(255, 32, 32, 0.9);
    width: 60px;
    height: 60px;
    border-radius: 50%;
    bottom: 15%;
    right: 8%;
    font-size: 2em;
    outline: none;
    border: 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}


/*------------------Desctop-------------------*/
@media (min-width: 1024px) { /* >= 1024px */
    ::-webkit-scrollbar { /* main scrollbar */
        width: 6px !important;
    }
    /*---------Header---------*/
    #app > header > div > a {
        margin: 0;
    }
    #mNavWrap {
        display: none;
    }
    header {
        height: 60px;
        padding: 0 50px;
    }
    #dNavWrap {
        height: 100%;
        display: block;
    }
    nav {
        height: 100%;
        width: 600px;
    }
    #menuToggleMobDiv {
        display: none;
    }
    #dNavWrap ul li a {
        height: 100%;
        display: flex;
        align-items: center;
        width: 100%;
        justify-content: center;
    }
    #dNavWrap ul li img {
        width: 20px;
        height: 20px;
        margin: 0 30px 0px 0;
    }
    #dNavWrap .menuWrap {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    #dNavWrap .menuWrap > li {
        height: 100%;
        width: 120px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        position: relative;
    }
    #dNavWrap .menuWrap > li i {
        font-size: 0.7em;
        margin-left: 8px;
    }
    #dNavWrap .menuWrap > li:hover {
        border-bottom: 2px solid rgb(204, 135, 45);
        background: rgba(22, 133, 219, 0.94);
        transition: background-color 0.1s linear;
        padding: 2px 0 0 0;
    }
    #dNavWrap .subMenuWrap {
        position: absolute;
        top: 60px;
        background: rgba(32, 152, 247, 0.94);
        left: 0;
        display: none;
    }
    #dNavWrap .transfersSubMenu li, #dNavWrap .othersSubMenu li {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.1em;
        width: 150px;
    }
    #dNavWrap .othersSubMenu li {
        width: 200px;
    }
    #dNavWrap .transfersSubMenu li img {
        margin: 0 15px 0 0px;
        width: 17px;
        height: 17px;
    }
    #dNavWrap .othersSubMenu li img {
        margin: 0 15px 0 0px;
        width: 16px;
        height: 14px;
    }
    #dNavWrap .subMenuWrap > li {
        height: 30px;
        padding: 0 10px 0 10px;
        position: relative;
        text-align: center;
    }
    #dNavWrap .subMenuWrap > li:hover {
        border-bottom: 2px solid rgb(204, 135, 45);
        background: rgba(22, 133, 219, 0.94);
        transition: background-color 0.1s linear;
        padding-top: 2px;
    }
    #dNavWrap .subMenuWrap > li:hover i {
        color: rgb(204, 135, 45);
    } 
    #dNavWrap .subMenuWrap > li:hover .subSubMenuWrap li i  {
        color: #fff;
    }
    #dNavWrap .subMenuWrap li a {
        font-size: 0.8em;
        width: 145px;
    }
    #dNavWrap .subMenuWrap li a i {
        font-size: 0.8em;
    }
    #dNavWrap .subSubMenuWrap {
        position: absolute;
        top: 0;
        background: rgba(32, 152, 247, 0.94);
        left: 165px;
        display: none;
        z-index: 20;
    }
    #dNavWrap .subSubMenuWrap > li {
        height: 30px;
        padding: 0 20px 0 20px;
    }
    #dNavWrap .subSubMenuWrap > li:hover {
        border-right: 3px solid rgb(204, 135, 45);
        background: rgba(22, 133, 219, 0.94);
        transition: background-color 0.1s linear;
        padding: 0 15px 0 15px;
        transition: 0.2s;
    }
    #dNavWrap .subSubMenuWrap li a {
        font-size: 0.88em;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 130px;
    }
    #dNavWrap .subSubMenuWrap li:hover a i {
        padding: 0 10px 0 0;
        transition: 0.2s;
    }
    #dNavWrap .subSubMenuWrap li a i {
        font-size: 1em;
        padding: 0 10px 0 0;
    }
    header img {
        width: 50px;
        height: 50px;
        order: 0;
    }
    header .socnetWrap {
        order: 2;
        width: 110px;
        font-size: 1.2em;
    }
    header .active:nth-child(1) {
        margin: 0;
    }
    main {
        padding: 150px 0 0 0;
    }
    main .container {
        width: 1000px;
    }
    #dNavWrap > ul > li:nth-child(2) > ul > li > ul > li:nth-child(4) > a > img {
        margin: 0 10px 0 0;
    }
    .favoriteTeamPopUp .close {
        bottom: 5%;
    }
}

@media (min-width: 1280px) { /* >= 1280px */
    .sidebar {
        display: flex;
        width: 100%;
    }
    .sidebar div div {
        display: block;
    }
    .sidebar:first-child {
        justify-content: flex-end;
        margin: 0 30px 0 0;
    }
    .sidebar:last-child {
        margin: 0 0 0 30px;
    }
    .sidebar h3 {
        color: #000;
        font-size: 0.85em;
        text-align: center;
    }
    .sidebar label, .sidebar span {
        color: #000;
    }
}

@media (min-width: 1680px) { /* >= 1680px */
    header .container {
        width: 1450px;
    }
    main .container {
        width: 1200px;
    }
}